
.input-custom {
    font-weight: 600;
}

.uppercase {
  text-transform: uppercase;
}

.day-custom {
    font-family: 'Roboto Condensed', sans-serif;
    font-weight: 600;
}

.input-custom:focus {
  border-color: #d3d3d3; /* Светло-серый цвет */
  box-shadow: 0 0 0 0.2rem rgba(211, 211, 211, 0.5);
}


.input-custom::placeholder {
  color: #b5b5b5; /* Светло-серый цвет */
  opacity: 1; /* Убедитесь, что он полностью видим */
}

.custom-select {
  background-color: #343a40; /* Темный фон */
  color: #f8f9fa; /* Светлый текст */
  border: 1px solid #495057; /* Рамка для контраста */
  border-radius: 0.25rem; /* Закругленные углы */
  padding: 10px;
  font-size: 1rem;
  transition: all 0.3s ease; /* Плавный переход для изменения состояния */
}

/* Эффект наведения */
.custom-select:hover {
  border-color: #007bff; /* Синяя рамка при наведении */
  background-color: #495057; /* Темнее фон при наведении */
}

/* Эффект при фокусе на поле */
.custom-select:focus {
  outline: none; /* Убираем стандартное выделение */
  border-color: #28a745; /* Зеленая рамка при фокусе */
  box-shadow: 0 0 5px rgba(40, 167, 69, 0.5); /* Мягкое свечение */
}

/* Для option - изменение стилей при выборе */
.custom-select option {
  background-color: #495057; /* Темный фон для опций */
  color: #f8f9fa; /* Светлый текст */
  padding: 5px;
  font-size: 1rem;
}

.custom-select option:hover {
  background-color: #007bff; /* Подсветка опции при наведении */
  color: #fff; /* Светлый текст */
}

.custom-select option:checked {
  background-color: #28a745; /* Зеленый фон для выбранных опций */
}